/* Base Reset */
body, html {
  margin: 0;
  padding: 0;
  font-family: 'Segoe UI', sans-serif;
  background: #f9f9f9;
}

/* Container */
.homepage-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
}

/* Header */
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #20232a;
  padding: 1rem;
  color: #fff;
  position: relative;
}

.title {
  font-size: 1.8rem;
  font-weight: bold;
}

.menu-toggle {
  font-size: 1.8rem;
  cursor: pointer;
  display: none;
}

/* Nav */
.side-nav {
  position: absolute;
  top: 100%;
  right: 0;
  background: #20232a;
  width: 200px;
  transform: translateX(100%);
  transition: transform 0.3s ease;
  z-index: 10;
}

.side-nav.open {
  transform: translateX(0);
}

.nav-list {
  list-style: none;
  padding: 0;
}

.nav-list li {
  padding: 1rem;
  border-bottom: 1px solid #444;
}

.nav-list li a {
  color: #fff;
  text-decoration: none;
}

.nav-list li a:hover {
  text-decoration: underline;
}

/* Hero */
.hero-section {
  background: linear-gradient(to right, #ffdde1, #ee9ca7);
  padding: 3rem 1rem;
  text-align: center;
  border-radius: 10px;
  margin: 1rem 0;
  color: #222;
}

.hero-title {
  font-size: 2rem;
  margin-bottom: 1rem;
}

.hero-text {
  font-size: 1.2rem;
}

.shop-now-btn {
  background: #ff4081;
  color: #fff;
  padding: 0.7rem 1.5rem;
  margin-top: 1rem;
  border: none;
  border-radius: 25px;
  font-weight: bold;
  text-decoration: none;
  display: inline-block;
}

/* Promo */
.promo-banner {
  background: #ffe082;
  padding: 1rem;
  text-align: center;
  margin-top: 1rem;
  font-weight: bold;
  border-radius: 5px;
}

/* Filter Bar */
.filter-bar {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  margin: 2rem 0;
}

.filter-bar input,
.filter-bar select {
  padding: 0.6rem;
  flex: 1;
  border-radius: 5px;
  border: 1px solid #ccc;
}

/* Categories */
.category-carousel {
  display: flex;
  gap: 1rem;
  overflow-x: auto;
  margin-bottom: 2rem;
}

.category-card {
  flex: 0 0 auto;
  width: 150px;
  text-align: center;
  border-radius: 10px;
  overflow: hidden;
  background: #fff;
  box-shadow: 0 2px 6px rgba(0,0,0,0.1);
}

.category-card img {
  width: 100%;
  height: 100px;
  object-fit: cover;
}

/* Products */
.products-section {
  margin: 2rem 0;
}

.section-title {
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.products-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 1rem;
}

.product-card {
  background: #fff;
  padding: 1rem;
  border-radius: 10px;
  box-shadow: 0 2px 6px rgba(0,0,0,0.1);
  text-align: center;
  transition: transform 0.2s ease;
}

.product-card:hover {
  transform: translateY(-5px);
}

.product-image {
  width: 100%;
  height: 150px;
  object-fit: cover;
}

.add-to-cart-btn {
  background: #2196f3;
  color: #fff;
  border: none;
  padding: 0.5rem 1rem;
  margin-top: 0.5rem;
  border-radius: 25px;
  cursor: pointer;
}

.add-to-cart-btn:hover {
  background: #1976d2;
}

/* Newsletter */
.newsletter {
  background: #fff3e0;
  padding: 2rem;
  border-radius: 10px;
  text-align: center;
  margin: 2rem 0;
}

.newsletter input {
  padding: 0.7rem;
  border-radius: 5px;
  border: 1px solid #ccc;
  margin-right: 1rem;
}

.newsletter button {
  background: #ff7043;
  color: white;
  border: none;
  padding: 0.7rem 1.5rem;
  border-radius: 5px;
  cursor: pointer;
}

.newsletter .error {
  color: red;
  margin-top: 0.5rem;
}

/* Reviews */
.customer-reviews {
  margin: 2rem 0;
  background: #f1f8e9;
  padding: 1.5rem;
  border-radius: 10px;
}

.review-card {
  background: #fff;
  padding: 1rem;
  border-radius: 5px;
  margin: 0.5rem 0;
  box-shadow: 0 1px 4px rgba(0,0,0,0.05);
}

/* Floating Cart */
.floating-cart {
  position: fixed;
  bottom: 20px;
  right: 20px;
  background: #ff4081;
  color: white;
  padding: 0.7rem 1rem;
  border-radius: 25px;
  text-decoration: none;
  font-weight: bold;
  box-shadow: 0 4px 8px rgba(0,0,0,0.2);
  transition: background 0.3s ease;
}

.floating-cart:hover {
  background: #e91e63;
}

/* Footer */
.footer {
  text-align: center;
  padding: 1rem;
  margin-top: 2rem;
  color: #555;
}

/* Responsive */
@media (max-width: 768px) {
  .menu-toggle {
    display: block;
  }

  .side-nav {
    position: fixed;
    right: 0;
    top: 60px;
    height: 100%;
    width: 200px;
    background: #20232a;
    transform: translateX(100%);
    transition: transform 0.3s ease-in-out;
  }

  .side-nav.open {
    transform: translateX(0);
  }

  .filter-bar {
    flex-direction: column;
  }

  .products-grid {
    grid-template-columns: 1fr;
  }
}
