.login-container {
  max-width: 400px;
  margin: 60px auto;
  padding: 30px;
  background-color: #ffffff;
  border-radius: 20px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.08);
  text-align: center;
}

.login-container h2 {
  margin-bottom: 20px;
  font-size: 28px;
  color: #222;
}

.login-container form {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.login-container input {
  padding: 12px;
  border: 1px solid #ddd;
  border-radius: 10px;
  font-size: 16px;
}

.login-container input:focus {
  outline: none;
  border-color: #ff5f6d;
  box-shadow: 0 0 4px rgba(255, 95, 109, 0.4);
}

.login-container button {
  padding: 12px;
  background: linear-gradient(to right, #ef4444, #ec4899);
  border: none;
  color: white;
  font-size: 16px;
  border-radius: 10px;
  cursor: pointer;
  transition: background 0.3s ease;
}

.login-container button:hover {
  background: linear-gradient(to right, #ff5f6d, #ffc371);
}

.login-container .error {
  color: red;
  font-size: 14px;
  margin-top: 5px;
}
.error {
  color: red;
  font-size: 14px;
  margin-top: 4px;
}

.register-link {
  margin-top: 15px;
  text-align: center;
  font-size: 14px;
}

.register-link a {
  color: #007bff;
  text-decoration: none;
}

.register-link a:hover {
  text-decoration: underline;
}
